.approach {
  margin: -88px auto 150px;
  padding-left: 121px;
  max-width: 1600px;
  color: #fff;

  &__content {
    display: flex;
    justify-content: space-between;
    border-radius: 200px 0 0 200px;
    padding: 204px 0 0 204px;
    background-color: #ff9c8e;

    width: 100%;
    min-height: 976px;
    position: relative;
    z-index: -1;
  }

  &__info {
    display: flex;
    flex-direction: column;
    max-width: 362px;

    align-self: flex-start;
  }

  &__title {
    font-weight: 700;
    letter-spacing: 3px;
    text-transform: uppercase;

    margin-bottom: 16px;
  }

  &__heading {
    font-weight: 600;
    font-size: 32px;

    margin-bottom: 32px;
  }

  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;

    margin-bottom: 64px;
  }

  &__point {
    position: relative;
    width: 64px;
    height: 64px;
    border-radius: 50%;
    background-color: #fff;
  }

  &__point-svg {
    width: 24px;
    height: 24px;
    object-fit: contain;

    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  &_blue-filter {
    filter: invert(44%)
      sepia(38%)
      saturate(6570%)
      hue-rotate(190deg)
      brightness(101%)
      contrast(108%);
  }

  &__text {
    max-width: 264px;
    font-size: 15px;
  }

  &__img {
    position: absolute;
    bottom: 0;
    right: 0;

    border-radius: 200px 0 0;
  }
}
